import {useEffect ,useState, Fragment} from "react";
import {connect} from "react-redux";
import Timer from "../UI/timer/timer";
import classes from './logOut.module.css'
import {SET_ATTENDANCE} from "../../store/actions/actions";
import postAsync from "../../store/actions/postAsync";
import getAsync from "../../store/actions/getAsync";

function LogOut(props){
    let date1 =new Date()
    let date =new Date().getTimezoneOffset()
    let today_record = null ;
    let {dispatch} = props;
    const [isAllowed , setAllowed] = useState(true)
        useEffect(()=>{
            dispatch(getAsync('/user/registerOut',SET_ATTENDANCE))
        },[dispatch])
        let register =()=>{
            props.dispatch(postAsync('/user/registerOut' ,{date ,hours:date1.getHours() ,minutes:date1.getMinutes()},SET_ATTENDANCE))
        }
        if(props.Attendance){
             today_record = new Date(props.Attendance.LeftAt)
        }
        if(props.Attendance ===null){
            return <h1>you need to register in first</h1>
        }
    return(
    <div className={classes.container}>
        {props.Attendance?.LeftAt ?
                <div>
                <h1 style={{textAlign:'center'}}>you already registered out today</h1>
                 <div className={classes.box}>
                <img style={{borderRadius:'50%'}} src={`https://www.gravatar.com/avatar/${props.user.avatar}`} alt='profile ' />
                <h2 style={{textAlign:'center'}}>{props.Attendance.name}</h2>
                <h2>
                    <span>{today_record.getHours()>12 ?
                        today_record.getHours()-12 +':'+today_record.getMinutes() +' PM'
                        :
                        today_record.getHours()+':'+today_record.getMinutes() +' AM'    
                }</span>
                </h2>
                <h2>
                    <span>{today_record.getFullYear()}</span>
                    <span>/{today_record.getMonth()+1}</span>
                    <span>/{today_record.getDate()}</span>
                </h2>
                 </div> 
                </div>   
            :
                <Fragment>
                <h1 style={{textAlign:'center'}}>
                <Timer setAllowed={setAllowed} xx={props.Attendance.AttendAt}/>
                </h1>
                <div style={{display:'flex' , justifyContent:'center' , alignItems:'center',flex:'1 0 0'}}>
                    <button disabled={isAllowed} onClick={register} style={{width:'50%', margin:0}}>Regiester</button>
                </div>
                <h2> you can register out after 8 hours</h2>
                <h2>at {new Date(+new Date(props.Attendance?.AttendAt)+8*60*60*1000).toLocaleTimeString()} </h2>

                </Fragment>
            
                 
        }
    </div>

    )
} 
const mapStateToprop =(state)=>({
    Attendance :state.Attendance    , 
    user :state.user  

})

export default connect(mapStateToprop)(LogOut)